import React, { Component } from 'react';
import './App.css';
import Gilakebi from "./Gilakebi"
class App extends Component {
    constructor(props){
        super(props);

        this.state = {
            result: ""
        }
    }

    onClick = button => {

        if(button === "="){
            this.daangarisheba()
        }

        else if(button === "C"){
            this.washla()
        }
        else if(button === "CE"){
            this.ertismoshoreba()
        }

        else {
            this.setState({
                result: this.state.result+button
            })
        }
    };
    daangarisheba(){
      this.setState({
        result:eval(this.state.result)
      })
    }
washla(){
  this.setState({
    result:""
  })
}
ertismoshoreba(){
this.setState({
  result:this.state.result.slice(0,-1)
})
}

    render() {
        return (
           
                <div className="calculatori">      
                    <EKRANI result={this.state.result}/>
                    <Gilakebi onClick={this.onClick}/>
                </div>
           
        );
    }
}
class EKRANI extends Component {
    render() {
        return (
            <div>
                კალკულატორი <br/>
            {this.props.result}
            </div>
        )
    }
}
export default App;